<template>
  <div class="main_navs">
      <div>
          <div class="org_name">
            <div class="org_text">
                Madaniy meros agentligi
            </div>
            <div class="org_title">
              Yagona elektron platforma
            </div>
          </div>
      </div>
      <div>
          <div class="user_data">
            <div>
              <img src="../assets/images/user.png" width="30px" alt="">
            </div>
            <div class="user_names">
              <div class="user">
                  {{user.first_name}}
              </div>
            </div>

        <button @click="logOut()" class="btn  out-btn pull-left" href="/oz/site/logout">
            <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="15" height="20" viewBox="0 0 18 20">
              <image id="_157938" data-name="157938" width="18" height="20" xlink:href="data:image/png;base64,"/>
            </svg>

        </button>
          </div>
      </div>
  </div>
</template>
<script>
export default {
  name: "MainNavbar", // Yangi nom
  methods:{
    logOut(){
      localStorage.removeItem('token')
      this.$router.push('/login')
    }
  },
  data(){
    return{
      user:''
    }
  },
  mounted() {
      this.$http.get('/user-me/',
        {
             headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
          })
          .then(res=>{
            this.user=res.data
          })
  }
};
</script>